<script>
import global from "../global";
import anime from "../../../modules/anime";
import Logo from "../../components/logo/Logo";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";

let idea_anime_end = false;
let guide_anime;

//export default
export default {
  name: "Home",
  data() {
    return {
      global: global,
      idea: [],
    };
  },
  components: {
    Logo,
    Menu,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.refresh_windows_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    window.onresize = () => {
      return (() => {
        global.refresh_windows_size();
      })();
    };
    Logo.methods.change_color("#251714", "#efcd24", 0);
    this.start_idea_anime();
    this.start_guide_anime();
    document.getElementById("home_page").addEventListener('wheel', this.handleScroll);
  },
  destroyed() {
    idea_anime_end = true;

  },
  watch: {},
  methods: {
    start_idea_anime() {
      let that = this;
      let idx = 0;
      let idea_list = this.global.lang_pack.home_idea_list;
      idea_anime_end = false;
      let change_fun = function () {
        let idea = idea_list[idx];
        idx++;
        idx %= idea_list.length;
        that.idea = [];
        if (document.getElementById("idea_bg") === null)
          return
        document.getElementById("idea_bg").style.width = "0px";
        for (let i = 0; i < idea.length; i++) {
          that.idea.push({
            idx: i,
            letter: idea[i],
            visibility: "hidden",
          });
        }

        for (let i = 0; i < idea.length; i++) {
            setTimeout(() => {
            that.idea[i].visibility = "visible";
            if (i === idea.length - 1) {
              if (document.getElementById("idea_box") === null)
                return
              let bg_width = document.getElementById("idea_box").clientWidth;
              new anime({
                targets: "div.idea_bg",
                width: "100%",
                delay: 100,
                duration: idea.length * 100,
                easing: "linear",
              });
            }
          }, i * 150);
        }

        if (idea_anime_end === false) {
          let t = {t}
          new anime({
            targets: t,
            t: [0,0],
            duration: idea.length * 250 + 200,
            easing: "linear",
            complete: function (){
              change_fun()
            }
          });
        }
      };
      change_fun();
    },
    start_guide_anime() {
      new anime({
        targets: "div.guide_ball",
        opacity: [
          {value: ["0%", "100%"], duration: 250},
          {value: ["100%", "0%"], delay: 750, duration: 250},
        ],
        scaleY: [
          {value: 2, duration: 250, delay: 250},
          {value: 1, duration: 250},
        ],
        scaleX: [
          {value: 0.5, duration: 250, delay: 250},
          {value: 1, duration: 250},
        ],
        translateY: [
          {
            value: "30px",
            duration: 250,
            delay: 250,
          },
          {
            value: "70px",
            duration: 250,
          },
        ],
        // direction: "alternate",
        endDelay: 250,
        loop: true,
        easing: "linear",
      });
    },
    handleScroll(e) {
      this.scroll = document.documentElement.scrollTop || document.body.scrollTop
      if (e.deltaY > 30)
        this.to_stories()
    },

    to_stories() {
      this.$router.push("/Stories");
    }
  },
};
</script>



